* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
}
html,
body {
  width: 100%;
  height: 100%;
  background-color: #ecf0f1;
}
#root,
.maindiv {
  height: 100%;
}
:root {
  --font1: 'Raleway';
  --font2: 'Nunito';
}
nav {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  font-family: var(--font2);
  height: 70px;
  padding: 0 30px;
  box-shadow: 0 0 5px #2d3436;
  background-color: #353b48;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}
.centerul {
  display: flex;
  align-items: center;
  justify-content: center;
}
nav ul {
  display: flex;
}
nav li {
  margin: 13px;
  list-style: none;
  font-weight: 500;
  font-size: 19px;
  padding: 2px;
}
nav a {
  color: #fff;
  text-decoration: none;
}
nav h1 {
  font-size: 30px;
  color: #fff;
}
ul img:nth-of-type(1) {
  width: 30px;
  height: 30px;
}
.fakenav {
  height: 70px;
  width: 100%;
}
.spec {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.spec a {
  color: #ffeaa7;
}
.spec img {
  width: 35px;
  height: 35px;
  margin-right: 5px;
}
#control {
  width: 30px;
  height: 30px;
  position: fixed;
  top: 90px;
  right: 20px;
}
#control:hover {
  cursor: pointer;
}
/* 460 */
.first-section {
  height: calc(100% - 70px);
  padding: 120px 0;
  font-family: var(--font2);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  background-color: #ffeaa7;
  justify-content: center;
  min-height: 460px;
}
.first-section h1 {
  font-weight: 700;
  font-size: 80px;
  color: #2d3436;
  text-align: center;
}
.first-section h2 {
  font-weight: 700;
  font-size: 50px;
  text-align: center;
  padding-top: 15px;
  color: #2d3436;
}
.flexbutton {
  display: flex;
  justify-content: center;
  margin-top: 30px;
}
.flexbutton button {
  margin: 10px;
  width: 140px;
  height: 50px;
  font-size: 18px;
  color: #fff;
  border: none;
  border-radius: 5px;
}
.flexbutton button:nth-of-type(1) {
  background-color: #2a2c39;
}
.flexbutton a button:nth-of-type(1) {
  background-color: #8244f7;
}
.second-section {
  font-family: var(--font2);
  padding: 50px 30px;
  background-color: #ecf0f1;
  max-width: 1500px;
  margin: auto;
}
.second-section h1 {
  font-size: 80px;
  -webkit-text-stroke: 2px #2d3436;
  color: transparent;
}
.second-section p {
  color: #2d3436;
}
.info {
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 50px auto;
  border-radius: 10px;
  background-color: rgba(0, 184, 148, 0.3);
}
.text {
  width: 50%;
}
.text p {
  line-height: 2;
}
.third-section {
  font-family: var(--font2);
  padding: 50px 30px;
  background-color: #ecf0f1;
  background-color: #ffeaa7;
}
.third-section h1 {
  font-size: 80px;
  -webkit-text-stroke: 2px #2d3436;
  color: transparent;
  text-align: center;
}
.flex-items {
  display: flex;
  width: 100%;
  padding-top: 70px;
  justify-content: center;
  flex-wrap: wrap;
  max-width: 1400px;
  margin: auto;
}
.box {
  flex-shrink: 0;
  width: 300px;
  height: 350px;
  margin: 30px;
  /* #2d3436 */
  background: #353b48;
  padding: 10px;
  border-radius: 20px;
  overflow: hidden;
  color: #fff;
  font-family: var(--font2);
}
.box h2 {
  margin-top: 5px;
}
.img2 {
  width: 100%;
  margin: auto;
  height: 230px;
  margin-bottom: 2px;
}
.box button {
  width: 100px;
  height: 40px;
  margin-top: 20px;
  font-size: 18px;
  border-radius: 10px;
  border: none;
  background-color: #ffeaa7;
  color: #fff;
}
.box button {
  margin-right: 20px;
  background-color: #ffeaa7;
  color: #000;
}
.b2 {
  background-color: #8244f7 !important;
  color: #fff !important;
}
.skills-section {
  padding: 50px 30px;
  background-color: #ecf0f1;
  font-family: var(--font2);
}
.skills-section h1 {
  font-size: 80px;
  -webkit-text-stroke: 2px #2d3436;
  color: transparent;
  text-align: center;
}
.flex-items2 {
  display: flex;
  justify-content: center;
  padding-top: 50px;
  flex-wrap: wrap;
  margin: auto;
}
.box2 {
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #2a2c39;
  color: #ecf0f1;
  margin: 25px;
  padding: 20px;
  border-radius: 10px;
}
.box2 img {
  width: 65px;
  height: 65px;
}
#pdf {
  width: 90%;
  height: 85%;
  position: fixed;
  top: calc(7.5% + 10px);
  left: 5%;
  z-index: 99;
  border-radius: 10px;
}
#close {
  width: 40px;
  height: 40px;
  background-color: crimson;
  color: #fff;
  font-size: 18px;
  border-radius: 50%;
  position: fixed;
  top: 5px;
  right: 5px;
  z-index: 99;
  border: none;
}
.mobilebar a {
  color: #fff;
  display: block;
  text-decoration: none;
}
.mobilebar {
  font-family: var(--font2);
  width: 200px;
  height: 200px;
  background: #2d3436;
  position: fixed;
  display: flex;
  flex-direction: column;
  top: 125px;
  right: 20px;
  align-items: center;
  justify-content: space-evenly;
  border-radius: 10px;
  transform: scale(0);
  transition: 0.5s;
}
.activ {
  transform: scale(1);
}
.mobilebar {
  display: none;
}
@media (max-width: 1000px) {
  .info {
    flex-direction: column;
  }
  .text {
    width: 85%;
    padding: 30px 0;
  }
  .text h1 {
    font-size: 40px;
  }
}
@media (max-width: 900px) {
  .centerul {
    display: none;
  }
  nav {
    grid-template-columns: repeat(2, 1fr);
  }
  .mobilebar {
    display: flex;
  }
  .info img {
    width: 60%;
    height: 75%;
  }
  .first-section h1,
  .second-section h1,
  .third-section h1,
  .skills-section h1 {
    font-size: 45px;
    width: 97%;
    margin: auto;
    text-align: center;
  }
  .text h1 {
    text-align: left;
  }
  .first-section h2 {
    font-size: 29px;
    width: 97%;
    margin: auto;
    padding-top: 20px;
  }
  .flexbutton button {
    width: 120px;
  }
  .box2 {
    padding: 50px;
  }
}
@media (max-width: 500px) {
  .info img {
    width: 95%;
    height: 95%;
  }
  .info {
    width: 100%;
  }
}
::-webkit-scrollbar {
  width: 3px;
}
::-webkit-scrollbar-thumb {
  background-color: #ffeaa7;
}
.img2 {
  background-size: cover;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.5);
  background-blend-mode: darken;
}
.im1 {
  background-image: url('./works/w3.png');
}

.im2 {
  background-image: url('./works/w2.png');
}

.im3 {
  background-image: url('./works/w1.png');
}

.im4 {
  background-image: url('./works/w4.png');
}

.im5 {
  background-image: url('./works/w7.png');
}

.im6 {
  background-image: url('./works/w6.png');
}
.im7 {
  background-image: url('./works/w5.jpg');
}
button:hover {
  cursor: pointer;
}
a:hover {
  cursor: pointer;
}
footer {
  padding: 50px 0 0 0;
  background-color: #ffeaa7;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: var(--font2);
}
footer p {
  width: 90%;
  text-align: center;
  padding: 20px 0;
}
footer button {
  height: 40px;
  font-size: 17px;
  padding: 0 5px;
  background-color: #8244f7;
  border: none;
  border-radius: 5px;
  color: #fff;
}
@media (min-width: 1500px) and (min-height: 900px) {
  .first-section {
    height: 800px;
  }
}
